import React from "react";
import { Link } from "react-router-dom"; // a    css  a  .
import "./Navbar.css";

const Navbar = () => {
  return (
    <nav className="navbar">
      <Link to="/">Home</Link>
      <Link to="/gallery">Gallery</Link>
      <Link to="/scroll">ScrollHere</Link>
      <Link to="/guestbook">GuestBook</Link>
    </nav>
  );
};
export default Navbar;
